.container {
  height: 100%;
  display: flex;
  justify-content: center;
  padding: 40px 70px 70px 70px;
}

.movies {
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  grid-gap: 40px;
  padding-top: 40px;
}

@media screen and (max-width: 1090px) {
  .movies {
    width: 100%;
  }
}
